import React from 'react';
import { Comment, Avatar, Form, Button, List, Input } from 'antd';
import moment from 'moment';
const { TextArea } = Input;
const CommentList = ({ comments }) => (
	<List
		dataSource={comments}
		header={`${comments.length} ${comments.length > 1 ? '' : ''}`}
		itemLayout="horizontal"
		renderItem={props => <Comment {...props} />}
	/>
);
const Editor = ({ onChange, onSubmit, submitting, value }) => (
	<div>
		<Form.Item>
			<TextArea rows={4} onChange={onChange} value={value} placeholder=""/>
		</Form.Item>
		<Form.Item>
			<Button htmlType="submit" loading={submitting} onClick={onSubmit} type="primary">
				
			</Button>
		</Form.Item>
	</div>
);

class ZjComment extends React.Component {
	state = {
		comments: [],
		submitting: false,
		value: '',
	};

	componentWillReceiveProps(nextProps) {
		const {okrComments, delComment} = nextProps;
		if (okrComments != this.props.okrComments) {
			this.setState({
				comments: okrComments.map((item)=>{
					return Object.assign({}, {
						id: item.id,
						author: item.userName,
						avatar: <Avatar style={{backgroundColor: '#87d068'}} icon="user" />,
						content: <p>{item.text}</p>,
						datetime: moment(item.createTime).format("YYYY-MM-DD HH:mm:ss"),
						actions: [<span key="comment-list-reply-to-0" onClick={delComment.bind(this, item.id)}></span>],
					})
				})
			});
		}
	}

	componentDidMount() {
		this.props.onRef(this);
	}

	handleSubmit = () => {
		if (!this.state.value) {
			return;
		}

		this.setState({
			submitting: true,
		});

		setTimeout(() => {
			this.props.saveComment(this.state.value);
		}, 1000);
	};

	handleChange = e => {
		this.setState({
			value: e.target.value,
		});
	};

	clearState = () => {
		this.setState({
			submitting: false,
			value: ''
		});
	}

	render() {
		const { comments, submitting, value } = this.state;

		return (
			<div>
				{comments.length > 0 && <CommentList comments={comments} />}
				<Comment
					avatar={
						<Avatar
							src={"https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png"}
							//style={{backgroundColor: '#87d068'}}
							// icon="user"
						/>
					}
					content={
						<Editor
							onChange={this.handleChange}
							onSubmit={this.handleSubmit}
							submitting={submitting}
							value={value}
						/>
					}
				/>
			</div>
		);
	}
}

export default ZjComment;



