/*
* OKR
* dangwei@bacspace.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import * as mobx from 'mobx';
import moment from "moment";
import {Scrollbars} from 'react-custom-scrollbars';
import {Row, Col, Dropdown, Icon, Affix, Menu, Tooltip, Radio, Button, Form, Tabs, message, Modal, Popconfirm} from "antd";
import DingtalkTree from '@/components/bpm/DingtalkTree';
import OkrTable from '@/components/bpm/OkrTable';
import ZjComment from '@/components/bpm/ZjComment';
import BpmStore from "store/bpm/BpmStore";
import MyUpload from "@/components/worktile/MyUpload";

const {TabPane} = Tabs;
const {confirm} = Modal;

@inject('appStore')
@observer
export default class Okr extends React.Component {
	constructor(props) {
		super(props);
		this.store = new BpmStore();
		this.state = {
			years: [{
				id: '',
				name: '2019'
			}],  // 
			value: '2019',
			weekAddData: {   // 
				id: "",
				orders: "",
				keyResult: "",
				workItems: "",
				associationDescription: "",
				results: "",
			},
			yearAddData: {   // 
				id: "",
				orders: "",
				objective: "",
				uniformity: "",
				keyResult: "",
				depend: "",
				dependencyDescription: "",
				confidenceLevel: "",
				completion: "",
				score: "",
			},
			season: "01",
			key: '1',
			visible: false, // 
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {
		this.store.getSts();
		// 
		const currentYear = moment().year();
		/*
		* 2019 
		* */
		if (currentYear <= 2019) {
			this.setState({
				years: [{
					name: '2019'
				}]
			});
		} else {
			const num = Number(currentYear);
			let data = [];
			for (let i = 2019; i <= num; i++) {
				data.push({name: String(i)})
			}
			this.setState({
				years: Object.assign([], data.reverse()),
				value: currentYear.toString()
			}, () => {
				const {userInfo} = this.props.appStore;
				const userId = mobx.toJS(userInfo).id;
				const userName = mobx.toJS(userInfo).realname;
				this.store.userId = userId;
				this.store.userName = userName;
				this.getWeeks(currentYear);
			});
		}
	}

	getWeeks = (currentYear) => {
		this.store.getWeeks(currentYear).then((res) => {
			this.store.weeksData = Object.assign([], res);
			let activeweeks = res.filter(item => item.isDefault == true)[0];
			this.store.activeweeks = Object.assign({}, activeweeks);
			this.store.getWeekDetails(activeweeks.id)
			this.store.getMonthDetails(activeweeks.month)
			this.store.getAtts({
				type: 'week',
				code: activeweeks.id
			})
			this.store.getComments({
				type: 'week',
				code: activeweeks.id
			})
		});
	}

	onRef = (ref) => {
		this.child = ref
	}

	onCommentRef = (ref) => {
		this.comment = ref
	}

	// 
	handleYearClick = (e) => {
		this.setState({
			value: e.item.props.children
		}, () => {
			this.getWeeks(this.state.value);
		});
	}

	// 
	saveAttachment = (arr) => {
		const {key, season, value} = this.state;
		const {activeweeks} = this.store;
		const params = {
			type: "",
			code: "",
			attachments: arr.map((item) => {
				return Object.assign({}, {
					fileId: item.fileId,
					fileName: item.fileName,
					filePath: item.filePath,
					id: item.id
				})
			})
		}
		if (key == "1") {
			params.type = "week";
			params.code = mobx.toJS(activeweeks).id;
			this.store.saveAtt(params).then((res) => {
				if (res == true) {
					message.success("")
                    this.store.getAtts({
	                    type: 'week',
	                    code: activeweeks.id
                    })
				}
			});
		} else if (key == "2") {
			params.type = "season";
			params.code = value + season;
			this.store.saveAtt(params).then((res) => {
				if (res == true) {
					message.success("")
					this.store.getAtts({
						type: 'week',
						code: value + season
					})
				}
			});
		} else {
			params.type = "year";
			params.code = value;
			this.store.saveAtt(params).then((res) => {
				if (res == true) {
					message.success("")
					this.store.getAtts({
						type: 'week',
						code: value + season
					})
				}
			})
		}
	}

	// 
	delNode = (id) => {
		const {key, season, value} = this.state;
		const {activeweeks} = this.store;
		const params = {
			type: "",
			code: "",
			attachmentId: id
		}
		if (key == "1") {
			params.type = "week";
			params.code = mobx.toJS(activeweeks).id;
		} else if (key == "2") {
			params.type = "season";
			params.code = value + season;
		} else {
			params.type = "year";
			params.code = value;
		}
		this.store.delAtt(params).then((res) => {
			if (res == true) {
				message.success("")
			}
		});
	}

	callback = (type) => {
		this.setState({
			key: type
		});
		const {value, season} = this.state;
		switch (type) {
			case "1":
				this.getWeeks(value);
				break;
			case "2":
				const param = value + season;
				this.store.getSeasonDetails(param);
				this.store.getAtts({
					type: 'season',
					code: param
				})
				this.store.getComments({
					type: 'season',
					code: param
				})
				break;
			case "3":
				this.store.getYearDetails(value);
				this.store.getAtts({
					type: 'year',
					code: value
				})
				this.store.getComments({
					type: 'year',
					code: value
				})
				break;
			default:
				break;
		}
	}

	// 
	handleWeekClick = (type) => {
		let _this = this;
		const {activeweeks, weeksData, defaultWeeksData, weekData} = _this.store;
		const aweeks = JSON.parse(JSON.stringify(activeweeks));
		const wData = JSON.parse(JSON.stringify(weeksData));
		const dtWeeksData = JSON.parse(JSON.stringify(defaultWeeksData));
		const aWeeksData = JSON.parse(JSON.stringify(weekData));
		const index = wData.findIndex(item => item.id == aweeks.id);
		let activeIndex = 0;
		if (type == 1) { // 
			if (index == 0) {
				message.warn("");
				return false;
			}
			activeIndex = index - 1;
		} else {  // 
			if (index == wData.length - 1) {
				message.warn("");
				return false;
			}
			activeIndex = index + 1;
		}

		// 
		let defaultSource = dtWeeksData.map(item => {
			return {
				"orders": item.orders,
				"keyResult": item.keyResult,
				"workItems": item.workItems,
				"associationDescription": item.associationDescription,
				"results": item.results
			}
		});
		let currentSource = aWeeksData.map(item => {
			return {
				"orders": item.orders,
				"keyResult": item.keyResult,
				"workItems": item.workItems,
				"associationDescription": item.associationDescription,
				"results": item.results
			}
		});
		const isChange = JSON.stringify(defaultSource) == JSON.stringify(currentSource); // 
		if (isChange == false) {
			confirm({
				title: '?',
				onOk() {
					let active = wData[activeIndex];
					_this.store.activeweeks = Object.assign({}, active);
					_this.store.getWeekDetails(active.id);
					_this.store.getAtts({
						type: 'week',
						code: active.id
					});
					_this.store.getComments({
						type: 'week',
						code: active.id
					})
					if (activeweeks.month !== active.month) {
						// 
						_this.store.getMonthDetails(active.month)
					}
				},
				onCancel() {
				},
			});
		} else {
			let active = wData[activeIndex];
			_this.store.activeweeks = Object.assign({}, active);
			_this.store.getWeekDetails(active.id);
			_this.store.getAtts({
				type: 'week',
				code: active.id
			})
			_this.store.getComments({
				type: 'week',
				code: active.id
			})
			if (activeweeks.month !== active.month) {
				// 
				_this.store.getMonthDetails(active.month)
			}
		}
	}

	// 
	saveData = (type, dataSource) => {
		this.store[type + 'Data'] = Object.assign([], dataSource);
	}

	onChangeRadio = (e) => {
		const {value} = e.target;
		this.setState({
			season: value
		}, () => {
			const param = this.state.value + value;
			this.store.getSeasonDetails(param);
			this.store.getAtts({
				type: 'season',
				code: param
			})
			this.store.getComments({
				type: 'season',
				code: param
			})
		});
	}

	// 
	handleSubmit = () => {
		const {key, value, season} = this.state;
		switch (key) {
			case "1":
				this.store.saveWeekDetails();
				this.store.saveMonthDetails().then((res) => {
					if (res == true) {
						message.success("")
					}
				});
				break;
			case "2":
				const param = value + season;
				this.store.saveSeasonDetails(param).then((res) => {
					if (res == true) {
						message.success("")
					}
				});
				break;
			case "3":
				this.store.saveYearDetails(value).then((res) => {
					if (res == true) {
						message.success("")
					}
				});
				break;
			default:
				break;
		}
	}

	saveComment = (text) => {
		const {key, season, value} = this.state;
		const {activeweeks} = this.store;
		const params = {
			type: "",
			code: "",
			comment: {text: text}
		}
		if (key == "1") {
			params.type = "week";
			params.code = mobx.toJS(activeweeks).id;
			this.store.saveComment(params).then((res) => {
				if (res == true) {
					message.success("")
					this.comment.clearState();
					this.store.getComments({
						type: 'week',
						code: activeweeks.id
					})
				}
			});
		} else if (key == "2") {
			params.type = "season";
			params.code = value + season;
			this.store.saveComment(params).then((res) => {
				if (res == true) {
					message.success("")
					this.comment.clearState();
					this.store.getComments({
						type: 'season',
						code: value + season
					})
				}
			});
		} else {
			params.type = "year";
			params.code = value;
			this.store.saveComment(params).then((res) => {
				if (res == true) {
					message.success("")
					this.comment.clearState();
					this.store.getComments({
						type: 'year',
						code: value
					})
				}
			});
		}
	}

	// 
	delComment = (id) => {
		const {key, season, value} = this.state;
		const {activeweeks} = this.store;
		const params = {
			type: "",
			code: "",
			commentId: id
		}
		if (key == "1") {
			params.type = "week";
			params.code = mobx.toJS(activeweeks).id;
			this.store.delComment(params).then((res) => {
				if (res == true) {
					message.success("")
					this.store.getComments({
						type: 'week',
						code: activeweeks.id
					})
				}
			});
		} else if (key == "2") {
			params.type = "season";
			params.code = value + season;
			this.store.delComment(params).then((res) => {
				if (res == true) {
					message.success("")
					this.store.getComments({
						type: 'season',
						code: value + season
					})
				}
			});
		} else {
			params.type = "year";
			params.code = value;
			this.store.delComment(params).then((res) => {
				if (res == true) {
					message.success("")
					this.store.getComments({
						type: 'year',
						code: value
					})
				}
			});
		}
	}

	handleSelect = (keys) => {
		this.store.userId = keys["key"];
		this.store.userName = keys["title"];
		const {value, season} = this.state;
		const {key} = this.state;
		switch (key) {
			case "1":
				this.getWeeks(value);
				break;
			case "2":
				const param = value + season;
				this.store.getSeasonDetails(param);
				this.store.getAtts({
					type: 'season',
					code: param
				})
				this.store.getComments({
					type: 'season',
					code: param
				})
				break;
			case "3":
				this.store.getYearDetails(value);
				this.store.getAtts({
					type: 'year',
					code: value
				})
				this.store.getComments({
					type: 'year',
					code: value
				})
				break;
			default:
				break;
		}
	}

	// 
	handleDelete = (id) => {
		const {seasonData, yearData} = this.store;
		const {key} = this.state;
		if (key == "2") {
           const dataSource = JSON.parse(JSON.stringify(seasonData));
		   const data = dataSource.filter(item => item.id !== id)
           this.saveData("season", data);
		} else {
			const dataSource2 = JSON.parse(JSON.stringify(yearData));
			const data2 = dataSource2.filter(item => item.id !== id)
			this.saveData("year", data2);
		}
	}

	// 
	toggle = () => {
		this.setState({
			visible: !this.state.visible
		});
	}

	render() {
		const {key, years, value, weekAddData, yearAddData, season, visible} = this.state;
		const {ossData, activeweeks, weekData, monthData, seasonData, yearData, okrAtts, okrComments, userName} = this.store;
		const menu = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleYearClick}>
					{years.map((item, index) => {
						return (
							<Menu.Item key={'year' + index}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		const operations = (<div style={{
			width: '100%',
			height: 'auto',
			marginRight: '30px'
		}}>
			<span style={{fontSize: '14px', fontWeight: '700', marginRight: '30px'}}>OKR - {userName}</span>
			<Dropdown overlay={menu}>
				<Button style={{width: 82}}>
					{value} <Icon type="down"/>
				</Button>
			</Dropdown>
			<Button className="ml30" type="primary" onClick={this.handleSubmit}></Button>
		</div>);
		const columns = [
			{
				title: '',
				dataIndex: 'orders',
				editable: true,
				align: 'center',
				width: '130px',
				type: 'number'
			},
			{
				title: <div><br/>key Result</div>,
				dataIndex: 'keyResult',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title: '',
				dataIndex: 'workItems',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title: '',
				dataIndex: 'associationDescription',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title: '',
				dataIndex: 'results',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
		];
		const JiColumns = [
			{
				title: '',
				dataIndex: 'orders',
				align: 'center',
				width: '80px',
				render: (text, record) => {
					return (<div
						className="pr">
						<Popconfirm title="?" onConfirm={() => this.handleDelete(record.id)}>
							<Icon
								style={{
									width: '35px',
									color: '#F17E7E',
									fontSize: '20px',
									position: 'absolute',
									left: '-10px',
									top: '50%',
									transform: 'translateY(-50%)'
								}}
								type="minus-circle"/>
						</Popconfirm>
						{text}
					</div>);
				}
			},
			{
				title: <div>Objectives<br/></div>,
				dataIndex: 'objective',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title: <div><br/>OKR</div>,
				dataIndex: 'uniformity',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title: <div><br/>Key Result</div>,
				dataIndex: 'keyResult',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title: '',
				dataIndex: 'depend',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title: '',
				dataIndex: 'dependencyDescription',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title:  <div><br/>1-10</div>,
				dataIndex: 'confidenceLevel',
				editable: true,
				align: 'center',
				type: 'number'
			},
			{
				title: '',
				dataIndex: 'completion',
				editable: true,
				align: 'center',
				type: 'textarea'
			},
			{
				title: '',
				dataIndex: 'score',
				editable: true,
				align: 'center',
				type: 'number'
			},
		];
		const attachments = okrAtts != null ? okrAtts.map((vitem) => {
			return Object.assign({}, {
				uid: vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.fileId,
				fileName: vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id
			})
		}) : [];
		return (
			<Row className="pr">
				<Tooltip title={visible?"":""}>
					<Icon type={visible?"double-left":"double-right"}
					      onClick={this.toggle}
					      placement="top"
					      style={{
						      position: 'absolute',
						      top: visible?"10px":'8px',
						      left: visible?"-15px":0,
						      cursor: 'pointer',
						      zIndex: 2
					      }}/>
				</Tooltip>
				<Col span="5" className={visible?"hidden":""}>
					<Affix offsetTop={60}>
						<Scrollbars
							autoHide
							autoHideTimeout={1000}
							autoHideDuration={200}
							autoHeight
							autoHeightMin={0}
							autoHeightMax={'calc(100vh - 180px)'}
							thumbMinSize={30}
							universal={true}>
							<DingtalkTree
								userSearch={true}
								onRef={this.onRef}
								store={this.store}
								showLine={true}
								handleSelect={this.handleSelect}
							/>
						</Scrollbars>
					</Affix>
				</Col>
				<Col span={visible?"24":"19"} style={{borderLeft: visible?'none':'1px solid #EBEBEB', minHeight: '350px', paddingLeft: visible?0:'15px'}}>
					<Tabs size={"small"} className="okr_tabs" activeKey={key} onChange={this.callback}
					      tabBarExtraContent={operations}>
						<TabPane tab="" key="1">
							{/**/}
							<h4 style={{
								fontSize: '15px',
								fontWeight: '400',
								marginBottom: '15px'
							}}>{mobx.toJS(activeweeks).weekMonthName}</h4>
							<div className="clearfix mb10">
								<Tooltip placement="top" title={""}>
									<Icon onClick={this.handleWeekClick.bind(this, '1')} className="fl pointer"
									      style={{fontSize: '24px'}} type="step-backward"/>
								</Tooltip>
								<Tooltip placement="top" title={""}>
									<Icon onClick={this.handleWeekClick.bind(this, '2')} className="fr pointer"
									      style={{fontSize: '24px'}} type="step-forward"/>
								</Tooltip>
							</div>
							<OkrTable
								type="week"
								saveData={this.saveData}
								dataSource={mobx.toJS(weekData)}
								addData={weekAddData}
								columns={columns}/>
							<hr style={{marginTop: '0'}}/>
							{/**/}
							<h4 style={{
								fontSize: '15px',
								fontWeight: '400',
								marginBottom: '15px'
							}}>{mobx.toJS(activeweeks).monthName}</h4>
							<OkrTable
								type="month"
								saveData={this.saveData}
								dataSource={mobx.toJS(monthData)}
								addData={weekAddData}
								columns={columns}/>
						</TabPane>
						<TabPane tab="" key="2">
							<Radio.Group className="mb15" onChange={this.onChangeRadio} value={season}
							             buttonStyle="solid">
								<Radio.Button value="01"></Radio.Button>
								<Radio.Button value="02"></Radio.Button>
								<Radio.Button value="03"></Radio.Button>
								<Radio.Button value="04"></Radio.Button>
							</Radio.Group>
							<OkrTable
								type="season"
								saveData={this.saveData}
								dataSource={mobx.toJS(seasonData)}
								addData={yearAddData}
								columns={JiColumns}/>
						</TabPane>
						<TabPane tab="" key="3">
							<OkrTable
								type="year"
								saveData={this.saveData}
								dataSource={mobx.toJS(yearData)}
								addData={yearAddData}
								columns={JiColumns}/>
						</TabPane>
					</Tabs>
					<Form.Item label="" colon={false}>
						<MyUpload
							saveAttachment={this.saveAttachment}
							delNode={this.delNode}
							disabled={false}
							multiple={true}
							ossData={mobx.toJS(ossData)}
							fileList={attachments}
						/>
					</Form.Item>
					<ZjComment
						onRef={this.onCommentRef}
						okrComments={mobx.toJS(okrComments)}
						saveComment={this.saveComment}
						delComment={this.delComment}
					/>
				</Col>
			</Row>
		)
	}
}




