/*
* OKRTable
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import {Table, Input, Popconfirm, Form, Icon, message, InputNumber, ConfigProvider} from 'antd';
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);
const {TextArea} = Input;
const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}></p>
	</div>
);
import {v4 as uuidv4} from 'uuid';


class EditableCell extends React.Component {
	state = {
		editing: true,
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			handleSave({...record, ...values});
		});
	};
	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title, handleDelete, type} = this.props;
		const {editing} = this.state;
		if (type == 'number') {
			if (dataIndex=="orders") {  // 
				return editing ? (
					<Form.Item style={{margin: 0}}>
						{form.getFieldDecorator(dataIndex, {
							rules: [{
								required: false,
								message: `${title}`,
							}],
							initialValue: record[dataIndex],
						})(
							<div className="w clearfix">
								<Popconfirm title="?" onConfirm={() => handleDelete(record.id)}>
									<Icon
										style={{
											width: '35px',
											color: '#F17E7E',
											fontSize: '20px',
										}}
										type="minus-circle"/>
								</Popconfirm>
								<InputNumber
									style={{
										width: 'calc(100% - 35px)',
									}}
									defaultValue={record[dataIndex]}
									ref={node => (this.input = node)}
									onPressEnter={this.save}
									onBlur={this.save}
								/>
							</div>
						)}
					</Form.Item>
				) : (
					<div className="w clearfix">
						<Popconfirm title="?" onConfirm={() => handleDelete(record)}>
							<Icon
								style={{
									width: '35px',
									color: '#F17E7E',
									fontSize: '22px',
								}}
								type="minus-circle"/>
						</Popconfirm>
						<div
							className="editable-cell-value-wrap"
							style={{
								width: 'calc(100% - 35px)',
								minHeight: '31px'
							}}
						>
							{children}
						</div>
					</div>
				);
			} else {   // 
				return editing ? (
					<Form.Item style={{margin: 0}}>
						{form.getFieldDecorator(dataIndex, {
							rules: [{
								required: false,
								message: `${title}`,
							}],
							initialValue: record[dataIndex],
						})(
							<InputNumber
								defaultValue={record[dataIndex]}
								ref={node => (this.input = node)}
								onPressEnter={this.save}
								onBlur={this.save}
							/>
						)}
					</Form.Item>
				) : (
					<div className="w clearfix">
						<Popconfirm title="?" onConfirm={() => handleDelete(record)}>
							<Icon
								style={{
									width: '35px',
									color: '#F17E7E',
									fontSize: '22px',
									float: 'left'
								}}
								type="minus-circle"/>
						</Popconfirm>
						<div
							className="editable-cell-value-wrap"
							style={{
								float: 'right',
								width: 'calc(100% - 35px)',
								minHeight: '31px'
							}}
						>
							{children}
						</div>
					</div>
				);
			}
		} else if(type == 'textarea') {  // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<TextArea
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder=""
							autoSize={{minRows: 2, maxRows: 5}}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		} else {   // 
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder=""
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
				>
					{children}
				</div>
			);
		}
	};
	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

@observer
class OkrTable extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],
			addData: {},
		};
	}

	componentWillReceiveProps(nextProps) {
		const {dataSource, addData} = nextProps;
		if (dataSource != this.props.dataSource) {
           this.setState({
	           dataSource: dataSource,
	           addData: Object.assign({}, addData)
           });
		}
	}

	componentDidMount() {
		const {dataSource, addData} = this.props;
		this.setState({
			dataSource: dataSource,
			addData: Object.assign({}, addData)
		});
	}

	// 
	handleAdd = () => {
		const {dataSource} = this.state;
		const {addData, type} = this.props;
		const newData = Object.assign({}, addData, {id: "!" + uuidv4(), orders: dataSource.length+1});
		this.setState({
			dataSource: [...dataSource, newData],
		}, () => {
			// 
			this.props.saveData(type, this.state.dataSource);
		});
	};

	// 
	handleDelete = (id) => {
		const {type} = this.props;
		const dataSource = [...this.state.dataSource];
		this.setState({
			dataSource: dataSource.filter(item => item.id !== id)
		}, () => {
			// 
			this.props.saveData(type, this.state.dataSource);
		});
	}

	// 
	handleSave = (row) => {
		const {type} = this.props;
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.id === item.id);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: newData}, () => {
			// 
			this.props.saveData(type, this.state.dataSource);
		});
	}

	render() {
		const {dataSource} = this.state;
		const {columns} = this.props;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const massColu = columns!=undefined&&columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					type: col.type,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete
				}),
			};
		});
		return (
				<ConfigProvider renderEmpty={customizeRenderEmpty}>
					<Table
						components={components}
						rowClassName={() => 'editable-row'}
						className="expand_table bpm_table"
						bordered
						dataSource={dataSource}
						columns={massColu}
						pagination={false}
						size={"middle"}
						rowKey={row => row.id}
					/>
					<Icon onClick={this.handleAdd}
					      style={{margin: '16px 0', color: '#4BA4BE', fontSize: '22px'}}
					      type="plus-circle"/>
				</ConfigProvider>
		);
	}
}

export default OkrTable;




