import React from 'react';
import {observer} from 'mobx-react';
import {Tree, Input, message} from 'antd';
import _ from 'lodash';

const {TreeNode} = Tree;
const {Search} = Input;
let dataList = [];

@observer
class DingtalkTree extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			expandedKeys: [],
			autoExpandParent: true,
			checkedKeys: [],
			treeData: [],
			searchValue: '',
			filterTreeData: [], // 
			parent: [], //
			selectedKeys: [], // 
		}
	}

	componentDidMount() {
		// 
		this.store.getDepartmentAndUsers("").then((res) => {
			// 
			let parent = [];
			res.forEach((item,index)=>{
				if (item.userInfos == null) {
					parent.push(item.id)
				} else if (Array.isArray(item.userInfos)) {
					parent.push(item.id)
				}
			});
			res.map((item) => {
				if (item.userInfos == null) {
					// userInfos == null
					item.key = item.id;
					item.title = item.name;
					delete item.userInfos;
				} else if (Array.isArray(item.userInfos)) {
					const userInfos = item.userInfos;
					item.key = item.id;
					item.title = item.name;
					// userInfos To children
					item.children = userInfos.map(mitem => Object.assign({}, {
						key: mitem.id,
						title: mitem.realname,
						isLeaf: true
					}));
				}
			});
			const treeData = this.transFormTree(res);
			this.setState({
				treeData: treeData,
				parent: parent,
				selectedKeys: this.store.userId!=undefined?[this.store.userId]:[],
				expandedKeys: this.store.userId!=undefined?[this.store.userId]:[],
			}, () => {
				dataList = []; // 
			});
		});

		this.props.onRef(this);
	}

	// json
	transFormTree = (data) => {
		let result = [];
		if (!Array.isArray(data)) {
			return result
		}
		let map = {};
		data.forEach(item => {
			map[item.id] = item;
		});
		data.forEach(item => {
			let parent = map[item.parentId];
			if (parent) {
				(parent.children || (parent.children = [])).push(item);
			} else {
				result.push(item);
			}
		});
		return result;
	}

	// 
	onExpand = expandedKeys => {
		this.setState({
			expandedKeys,
			autoExpandParent: false,
		});
	};

	// 
	onCheck = (checkedKeys) => {
		// 
		const {searchValue, parent} = this.state;
		let isSearch = searchValue==null||searchValue==""||searchValue==-1?true:false;  // true   false 
        let array = [];
		// checkedKeys
		if (isSearch==false) {  // 
			array = checkedKeys.filter((item)=>{
			 	return !parent.includes(item)
			});
		} else {
			array = checkedKeys;
		}
		// 
		let params = Array.from(new Set([...this.state.checkedKeys, ...array]));

		this.setState({checkedKeys: isSearch?array:params}, () => {
			const {userSearch} = this.props;
			if (userSearch) { // 
				let personParams = JSON.parse(JSON.stringify(this.store.personParams));
				this.store.personParams = Object.assign(personParams, {
					ids: Object.assign([], this.state.checkedKeys)
				});
				this.store.getPersonScheduleResources();
			} else {  // 
				// 
				this.props.handPersonChange(this.state.checkedKeys);
			}
		});
	};

	renderTreeNodes = data =>
		data.map(item => {
			let {searchValue} = this.state;
			const index = item.title.indexOf(searchValue);
			const beforeStr = item.title.substr(0, index);
			const afterStr = item.title.substr(index + searchValue.length);
			const title =
				index > -1 ? (
					<span>
              {beforeStr}
						<span style={{color: '#f50'}}>{searchValue}</span>
						{afterStr}
            </span>
				) : (
					<span>{item.title}</span>
				);

			if (item.children) {
				return (
					<TreeNode title={title} key={item.key} dataRef={item}>
						{this.renderTreeNodes(item.children)}
					</TreeNode>
				);
			}
			return <TreeNode key={item.key} title={item.title}
			                 className={searchValue !== "" && item.title.includes(searchValue) ? "active_search" : ""}
			                 isLeaf={item.isLeaf} dataRef={item}/>;
		});

	onChange = (e) => {
		const {userSearch} = this.props;
		const value = userSearch ? e.target.value : e;  // 
		const val = _.trim(value);
		if (val !== '') {  // name
			this.store.getDepartmentAndUsers(val).then((res) => {
				// keys keys
				if (Array.isArray(res)) {
					res.map((item) => {
						if (item.userInfos == null) {
							// userInfos == null
							item.key = item.id;
							item.title = item.name;
							delete item.userInfos;
						} else if (Array.isArray(item.userInfos)) {
							const userInfos = item.userInfos;
							item.key = item.id;
							item.title = item.name;
							// userInfos To children
							item.children = userInfos.map(mitem => Object.assign({}, {
								key: mitem.id,
								title: mitem.realname,
								isLeaf: true
							}));
						}
					});
					const filterTreeData = this.transFormTree(res);
                    const expandedKeys = dataList.map((item) => {
						if (item.title.indexOf(val) > -1) {
							return this.getParentKey(item.title, filterTreeData);
						}
						return null;
					});
					this.setState({
						expandedKeys,
						searchValue: val,
						autoExpandParent: true,
						filterTreeData: filterTreeData,
					});
				}
			});
		} else {
			this.setState({
				searchValue: -1,
				autoExpandParent: false,
			});
		}
	};

	generateList = (data) => {
		for (let i = 0; i < data.length; i++) {
			const node = data[i];
			const key = node.key;
			dataList.push({key, title: node.title});
			if (node.children) {
				this.generateList(node.children);
			}
		}
	}

	getParentKey = (title, tree) => {
		let parentKey;
		for (let i = 0; i < tree.length; i++) {
			const node = tree[i];
			if (node.children) {
				if (node.children.some(item => item.title === title)) {
					parentKey = node.key;
				} else if (this.getParentKey(title, node.children)) {
					parentKey = this.getParentKey(title, node.children);
				}
			}
		}
		return parentKey;
	}

	// 
	onSelect = (selectedKeys, info) => {
        const {node} = info;
        const {props} = node;
        const {dataRef} = props;
		if (dataRef.parentId==undefined) { // 
			this.setState({
				selectedKeys: Object.assign([], selectedKeys)
			},()=>{
				// 
				//console.log('selected', selectedKeys, dataRef);
				this.props.handleSelect(dataRef);
			});
		} else {
			//message.warn("")
			return false;
		}
	};

	render() {
		const {searchValue,treeData, expandedKeys, autoExpandParent, checkedKeys, filterTreeData, selectedKeys} = this.state;
		this.generateList(treeData);
		const {userSearch, showLine} = this.props;
		const data = searchValue==null||searchValue==""||searchValue==-1?treeData:filterTreeData;

		return <div>
			<div className={userSearch ? "tc mb10" : "hidden"}>
				<Search
					placeholder=""
					onChange={(e) => this.onChange(e)}
					style={{width: '80%'}}
				/>
			</div>
			<Tree
				onExpand={this.onExpand}
				expandedKeys={expandedKeys}
				autoExpandParent={autoExpandParent}
				onCheck={this.onCheck}
				checkedKeys={checkedKeys}
				checkable={!showLine}
				showLine={showLine}
				onSelect={showLine?this.onSelect:null}
				selectedKeys={selectedKeys}
			>
				{this.renderTreeNodes(data)}
			</Tree>
		</div>
	}
}

export default DingtalkTree;



