let Config = {
// OKR
  okr: {
    getWeeks: `query($year: Int) {
		getWeeks(year: $year)
			{
			id
			name
			month
			monthName
			isDefault
			weekMonthName
		}
	}`, // 

    getWeekDetails: `query($code: Int, $userId: String) {
		getWeekDetails(code: $code, userId: $userId)
			{
			id
			weekId
			userId
			orders
			keyResult
			workItems
			associationDescription
			results
		}
	}`, // 

    getMonthDetails: `query($code: Int, $userId: String) {
		getMonthDetails(code: $code, userId: $userId)
			{
			id
			monthId
			userId
			orders
			keyResult
			workItems
			associationDescription
			results
		}
	}`, // 

    getSeasonDetails: `query($code: Int, $userId: String) {
		getSeasonDetails(code: $code, userId: $userId)
			{
			id
			seasonId
			userId
			orders
			objective
			uniformity
			keyResult
			depend
			dependencyDescription
			confidenceLevel
			completion
			score
		}
	}`, // 

    getYearDetails: `query($code: Int, $userId: String) {
		getYearDetails(code: $code, userId: $userId)
			{
			id
			yearId
			userId
			orders
			objective
			uniformity
			keyResult
			depend
			dependencyDescription
			confidenceLevel
			completion
			score
		}
	}`, // 

    saveWeekDetails: `mutation($list: [InputWeekDetail], $code: Int, $userId: String) {
		saveWeekDetails(list: $list, code: $code, userId: $userId)
	}`, // 

    saveMonthDetails: `mutation($list: [InputMonthDetail], $code: Int, $userId: String) {
		saveMonthDetails(list: $list, code: $code, userId: $userId)
	}`, // 

    saveSeasonDetails: `mutation($list: [InputSeasonDetail], $code: Int, $userId: String) {
		saveSeasonDetails(list: $list, code: $code, userId: $userId)
	}`, // 

    saveYearDetails: `mutation($list: [InputYearDetail], $code: Int, $userId: String) {
		saveYearDetails(list: $list, code: $code, userId: $userId)
	}`, // 

    getAtts: `query($type: String, $code: Int, $userId: String) {
		getAtts(type: $type, code: $code, userId: $userId)
			{
			id
			type
			typeId
			typeUserId
			userId
			fileId
			fileName
			fileType
			filePath
			createTime
		}
	}`, // 

    getComments: `query($type: String, $code: Int, $userId: String) {
		getComments(type: $type, code: $code, userId: $userId)
			{
			id
			type
			typeId
			typeUserId
			userId
			text
			createTime
			userName
		}
	}`, // 

    saveAtts: `mutation($type: String, $code: Int, $userId: String, $attachments: [InputAttachment]) {
		saveAtts(type: $type, code: $code, userId: $userId, attachments: $attachments)
	}`, // 

    saveComment: `mutation($type: String, $code: Int, $userId: String, $comment: InputComment) {
		saveComment(type: $type, code: $code, userId: $userId, comment: $comment)
	}`, // 

    delAtt: `mutation($type: String, $code: Int, $userId: String, $attachmentId: String) {
		delAtt(type: $type, code: $code, userId: $userId, attachmentId: $attachmentId)
	}`, // 

    delComment: `mutation($type: String, $code: Int, $userId: String, $commentId: String) {
		delComment(type: $type, code: $code, userId: $userId, commentId: $commentId)
	}`, // 

    getWeekAndMonthDetails: `query($weekCode: Int, $userId: String) {
		getWeekAndMonthDetails(weekCode: $weekCode, userId: $userId)
		{
			weekDetail
			{
				id
				weekId
				userId
				orders
				keyResult
				workItems
				associationDescription
				results
			}
			monthDetail
			{
				id
				monthId
				userId
				orders
				keyResult
				workItems
				associationDescription
				results
			}
		}
	}`, // 

    saveWeekAndMonthDetails: `mutation($weeks: [InputWeekDetail], $months: [InputMonthDetail], $weekCode: Int, $userId: String) {
		saveWeekAndMonthDetails(weeks: $weeks, months: $months, weekCode: $weekCode, userId: $userId)
	}`, // 

  }
}
export default Config;



